.movies .movie {

  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  
}



.movies .movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie img {
  margin : 50px 50px 50px 50px;
  width: 60%;
  height: 30%;
  /* position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025); */
}

.movie .movie__title {
  margin: 0;
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

.movie__data {
  display: table;
  float: left;
  position: relative;
  width: 415px;
  margin-left:80px;
}

.movie__director,
.movie__actor,
.movie__year,
.movie__rating {
  display: flex;
  align-items: center;
  color: #2c2c2c;
  font-size: 13px;
}

.movie__director span,
.movie__actor span,
.movie__year span,
.movie__rating span {
  flex: 0 0 80px;
  font-weight: 700;
}

.background{
  margin-top:10px;
  position: relative;
  max-height: 800px;
  overflow: hidden;
}

.background img{
  max-height: initial;
  margin-top:-30%;
  width:512px;
}

.movie_year{
  background-color:red;
  color: white;
  width:100px;
  margin: 10px;
  border-radius: 4px;
}

.info{
  position:absolute;
  bottom: 1px;
  width: 512px;
}
.blackBox{
  text-align: left;
  padding-left: 20px;
  padding-right:20px;
  background-color: rgba(0,0,0,0.8);
  color:white;
}

.movie_title{
  margin-bottom:3px;
}
.movie_genres{
  font-size: 0.75rem;
}
.movie_info_name{
  margin-bottom:3px;
}
.basic_info_wrap{
  margin-top:0px;
  display:flex;
  font-weight: bold;
  justify-content: space-between;
}

.summary_title{
  margin-bottom: 3px;
}

.movie_summary{
  height:160px;
  overflow-y:scroll;
  -ms-overflow-style: none;
}
.movie_summary::-webkit-scrollbar{
  display: none;
}